import React, { useState, useEffect, useMemo } from 'react';
import './DataSourcesStep.css';

const DataSourcesStep = ({ mockDataSources, selectedDataSource, setSelectedDataSource, availableFields }) => {
  const [isDropdownOpen, setDropdownOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');

  const handleSelectDataSource = (source) => {
    setSelectedDataSource(source);
    setDropdownOpen(false);
  };

  const filteredFields = useMemo(() => {
    if (!availableFields) return [];
    return availableFields.filter(field => 
      field.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [availableFields, searchTerm]);

  return (
    <div className="data-sources-container">
      <div className="panel data-source-selector" style={{ flexBasis: '40%' }}>
        <h2 className="panel-title">Data Sources</h2>
        <p className="panel-subtitle">Select the data sources you want to explore.</p>
        
        <div className="custom-select-wrapper">
          <label className="custom-select-label">Select Data Sources</label>
          <div className="custom-select-display" onClick={() => setDropdownOpen(!isDropdownOpen)}>
            {selectedDataSource ? (
              <span className="custom-select-chip">{selectedDataSource}</span>
            ) : (
              <span style={{ color: '#9e9e9e' }}></span>
            )}
            <svg height="20" width="20" viewBox="0 0 20 20" aria-hidden="true" focusable="false" style={{ fill: 'rgb(158, 158, 158)' }}><path d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615l-4.695 4.502c-0.533 0.481-1.141 0.446-1.574 0l-4.695-4.502c-0.408-0.418-0.436-1.17 0-1.615z"></path></svg>
          </div>
          {isDropdownOpen && (
            <div className="custom-select-dropdown">
              {mockDataSources.map(source => (
                <div key={source} className="custom-select-option" onClick={() => handleSelectDataSource(source)}>
                  {source}
                </div>
              ))}
            </div>
          )}
        </div>
        {selectedDataSource && <p className="source-count">1 source selected</p>}
      </div>

      <div className="panel available-fields" style={{ flexBasis: '60%' }}>
        <h2 className="panel-title">Available Fields</h2>
        <p className="panel-subtitle">These fields are available from your selected data sources.</p>
        
        <div className="search-bar">
          <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg>
          <input 
            type="text" 
            placeholder="Search fields..." 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        <table className="fields-table">
          <thead>
            <tr>
              <th>Field Name</th>
              <th>Type</th>
              <th>Source</th>
            </tr>
          </thead>
          <tbody>
            {filteredFields.map(field => (
              <tr key={field.name}>
                <td>{field.name}</td>
                <td>{field.type}</td>
                <td>{field.source}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default DataSourcesStep;
